import React, { Component } from "react";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  InputGroup,
  Button,
  FormControl
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
class Registration extends Component {
  render() {
    return (
      <div>
        <Container fluid>
          <Row>
            <Col
              xs="12"
              style={{
                padding: 50,
                paddingTop: "10vh",
                paddingBottom: "10vh",
                backgroundColor: "#ba0fb4"
              }}
            >
              <Card>
                <Card.Header>
                  <h1>Vendor Registration</h1>
                </Card.Header>
                <Card.Body>
                  <div className="d-flex flex-column justify-content-center align-items-center">
                    <img
                      style={{
                        width: "200px",
                        height: "200px",
                        objectFit: "cover",
                        borderRadius: "50%"
                      }}
                      src="https://www.rd.com/wp-content/uploads/2017/09/01-shutterstock_476340928-Irina-Bg-1024x683.jpg"
                    ></img>
                    <Form.Group className="mt-5" controlId="formGridAddress1">
                      <Form.Control type="file" />
                    </Form.Group>
                  </div>
                  <hr className="my-5"></hr>
                  <Form.Group controlId="formGroupEmail">
                    <Form.Label>Vendor Name</Form.Label>
                    <Form.Control type="text" placeholder="Enter Vendor Name" />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlSelect1">
                    <Form.Label>Vendor Type</Form.Label>
                    <Form.Control as="select">
                      <option value="Restaurant">Restaurant</option>
                      <option value="Cafe">Cafe</option>
                      <option value="Escape Room">Escape Room</option>
                      <option value="Event">Event</option>
                    </Form.Control>
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlSelect2">
                    <Form.Label>Branch</Form.Label>
                    <Form.Control as="select">
                      <option value="Main">Main</option>
                      <option value="Sub">Sub</option>
                    </Form.Control>
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <Form.Group controlId="exampleForm.ControlInput1">
                    <Form.Label>Email address</Form.Label>
                    <Form.Control
                      type="email"
                      placeholder="Enter your Email Address"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput2">
                    <Form.Label>Phone Number</Form.Label>
                    <Form.Control type="text" placeholder="Enter your Phone" />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput3">
                    <Form.Label>Reservation Fee</Form.Label>
                    <InputGroup className="mb-3">
                      <InputGroup.Prepend>
                        <InputGroup.Text>EGP</InputGroup.Text>
                      </InputGroup.Prepend>
                      <FormControl
                        type="number"
                        aria-label="Amount (to the nearest dollar)"
                      />
                    </InputGroup>
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <p>Location</p>
                  <hr className="my-5"></hr>
                  <h3 className="mb-5">Bank Details</h3>
                  <Form.Group controlId="exampleForm.ControlInput3">
                    <Form.Label>Bank Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter your Bank Name"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput7">
                    <Form.Label>Account Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter your Account Name"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput6">
                    <Form.Label>Account Number</Form.Label>
                    <Form.Control
                      type="number"
                      placeholder="Enter your Account Number"
                    />
                  </Form.Group>
                  <hr className="my-5"></hr>

                  <h3 className="mb-5">Change Password</h3>
                  <Form.Group controlId="exampleForm.ControlInput4">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Enter your Password"
                    />
                  </Form.Group>
                  <Form.Group controlId="exampleForm.ControlInput5">
                    <Form.Label>Confirm Password</Form.Label>
                    <Form.Control
                      type="password"
                      placeholder="Renter your Password"
                    />
                  </Form.Group>
                  <hr className="my-5"></hr>
                  <Button
                    onClick={() => {
                      this.props.history.push("/vendor/home");
                    }}
                    variant="primary"
                    size="lg"
                    block
                  >
                    Submit Registration
                  </Button>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default withRouter(Registration);
