import React, { Component } from "react";
import VendorSidebar from "../../../Components/VendorSidebar/VendorSidebar";
import {
  Card,
  Container,
  Row,
  Col,
  Form,
  InputGroup,
  FormControl,
  Button
} from "react-bootstrap";

export default class EditProfile extends Component {
  render() {
    return (
      <div>
        <VendorSidebar>
          <Container fluid>
            <Row>
              <Col xs="12" style={{ padding: 10 }}>
                <Card>
                  <Card.Header>
                    <h1>Profile Setting</h1>
                  </Card.Header>
                  <Card.Body>
                    <div className="d-flex flex-column justify-content-center align-items-center">
                      <img
                        style={{
                          width: "200px",
                          height: "200px",
                          objectFit: "cover",
                          borderRadius: "50%"
                        }}
                        src="https://www.rd.com/wp-content/uploads/2017/09/01-shutterstock_476340928-Irina-Bg-1024x683.jpg"
                      ></img>
                      <Form.Group className="mt-5" controlId="formGridAddress1">
                        <Form.Control type="file" />
                      </Form.Group>
                    </div>
                    <hr className="my-5"></hr>
                    <Form.Group controlId="formGroupEmail">
                      <Form.Label>Vendor Name</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter Vendor Name"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                      <Form.Label>Vendor Type</Form.Label>
                      <Form.Control as="select">
                        <option value="Restaurant">Restaurant</option>
                        <option value="Cafe">Cafe</option>
                        <option value="Escape Room">Escape Room</option>
                        <option value="Event">Event</option>
                      </Form.Control>
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlSelect2">
                      <Form.Label>Branch</Form.Label>
                      <Form.Control as="select">
                        <option value="Main">Main</option>
                        <option value="Sub">Sub</option>
                      </Form.Control>
                    </Form.Group>
                    <hr className="my-5"></hr>
                    <Form.Group controlId="exampleForm.ControlInput1">
                      <Form.Label>Email address</Form.Label>
                      <Form.Control
                        type="email"
                        placeholder="Enter your Email Address"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput2">
                      <Form.Label>Phone Number</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter your Phone"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Reservation Fee</Form.Label>
                      <InputGroup className="mb-3">
                        <InputGroup.Prepend>
                          <InputGroup.Text>EGP</InputGroup.Text>
                        </InputGroup.Prepend>
                        <FormControl
                          type="number"
                          aria-label="Amount (to the nearest dollar)"
                        />
                      </InputGroup>
                    </Form.Group>
                    <hr className="my-5"></hr>
                    <p>Location</p>
                    <hr className="my-5"></hr>
                    <h3 className="mb-5">Bank Details</h3>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Bank Name</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter your Bank Name"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Account Name</Form.Label>
                      <Form.Control
                        type="text"
                        placeholder="Enter your Account Name"
                      />
                    </Form.Group>
                    <Form.Group controlId="exampleForm.ControlInput3">
                      <Form.Label>Account Number</Form.Label>
                      <Form.Control
                        type="number"
                        placeholder="Enter your Account Number"
                      />
                    </Form.Group>
                    <hr className="my-5"></hr>
                    <Button variant="primary" size="lg" block>
                      Save
                    </Button>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
        </VendorSidebar>
      </div>
    );
  }
}
